.Header {
  padding: 60px 0 60px;
  width: 100vw;
}

.Header h1 span {
  color: var(--app-color-main);
  margin-left: 12px;
}

.Header h3 {
  font-size: 15px;
  margin: 20px 0 0;
  position: relative;
}

.Header h3::before {
  content: "";
  width: 30px;
  height: 2px;
  background-color: var(--app-color-main);
  position: absolute;
  left: -50px;
  top: 9px;
}
.Header h3::after {
  content: "";
  width: 30px;
  height: 2px;
  background-color: var(--app-color-main);
  position: absolute;
  right: -50px;
  top: 9px;
}

@media screen and (max-width: 560px) {
  .Header h3::before,
  .Header h3::after {
    content: none;
  }
  .Header h3 {
    padding: 0 10px 0 10px;
  }
}
@media screen and (max-width: 480px) {
  .Header h1 {
    font-size: 35px;
  }
  .Header h3 {
    font-size: 14px;
  }
}
